.card {
  background: rgb(50, 61, 207);
  border-radius: 3px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 200; 
  -webkit-box-shadow: 0 3px 6px #000, 0 3px 6px #000; 
          box-shadow: 0 3px 6px #000, 0 3px 6px #000;
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  -o-transition: transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}

.card:hover {
  cursor: pointer;
  -webkit-box-shadow: 0 0 20px #ccc, 0 0 20px #ccc;
          box-shadow: 0 0 20px #ccc, 0 0 20px #ccc;
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

.flexContainer {
  padding: 10px;
  margin: 20px;
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.flexItem {
  padding: 5px;
  width: 200;
  height: 200;
  margin-top: 10px; 
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
}








